.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}

.sr-only-focusable:active, .sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto;
}

/*------------------------------------*    #tools.pxh-animations
\*------------------------------------*/
/*------------------------------------*    #tools.pxh-mixins
\*------------------------------------*/
/*------------------------------------*    #elements.body
\*------------------------------------*/
html {
  font-size: 0.9375em;
  line-height: 1.3333333333;
  background-color: #425866;
  color: black;
}

html, body {
  font-family: "GE Inspira Sans", "Helvetica", "Arial", sans-serif;
  height: 100%;
  width: 100%;
  overflow: hidden;
}

body {
  min-height: 100%;
}

/*------------------------------------*    #elements.links
\*------------------------------------*/
a {
  color: #3e87e8;
  text-decoration: underline;
}

a:hover {
  color: #3e87e8;
  text-decoration: none;
}

/*------------------------------------*    #elements.hr
\*------------------------------------*/
hr {
  height: 1px;
  color: #748b99;
  background: #748b99;
  font-size: 0;
  border: 0;
  margin-top: 15px;
  margin-bottom: 15px;
}

/*------------------------------------*    #components.pxh-badge
\*------------------------------------*/
.pxh-badge {
  opacity: 1;
  padding: 2px 4px 0px 4px;
  font-size: 13px;
  text-align: center;
  background-color: #96a8b2;
  display: inline-block;
  font-weight: normal;
  color: white;
  line-height: 1;
  border-style: solid;
  border-width: 1px;
  border-color: #96a8b2;
  border-radius: 6px;
  text-decoration: none;
  transition: opacity 250ms ease-in-out;
  transition-delay: 250ms;
}

.pxh-badge--navigation {
  background-color: #086e87;
  border-color: #086e87;
}

.pxh-badge--primary {
  background-color: #3e87e8;
  border-color: #3e87e8;
}

.pxh-badge--important {
  background-color: #e53838;
  border-color: #e53838;
}

.pxh-badge--warning {
  background-color: #dd6b1f;
  border-color: #dd6b1f;
}

.pxh-badge--error {
  background-color: #e8a736;
  border-color: #e8a736;
}

.pxh-badge--info {
  background-color: #4364dd;
  border-color: #4364dd;
}

.pxh-badge--healthy-black {
  background-color: black;
  border-color: black;
}

.pxh-badge--healthy-white {
  background-color: white;
  border-color: #96a8b2;
  color: black;
}

.pxh-badge--truncated {
  max-width: 45px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.pxh-badge--hidden {
  opacity: 0;
  margin: 0;
  padding: 0;
  font-size: 0;
  border: 0;
}

@media (min-width: 64em) {
  .pxh-badge--visible\@lg {
    opacity: 1;
    padding: 2px 4px 0px 4px;
    font-size: 13px;
  }
}

@media (min-width: 48em) {
  .pxh-badge--hidden\@md {
    opacity: 0;
    margin: 0;
    padding: 0;
    font-size: 0;
    border: 0;
  }
}

/*------------------------------------*    #components.pxh-closer
\*------------------------------------*/
.pxh-closer {
  height: 100%;
  width: 100%;
  position: absolute;
  background-color: transparent;
  opacity: 0;
}

.pxh-closer--invisible {
  opacity: 0;
}

.pxh-closer--opaque {
  opacity: 1;
}

.pxh-closer--transparent {
  opacity: 0.5;
}

