  margin: 0;
}

.pxh-spinner::after {
  content: ' ';
  display: inline-block;
  font-size: 1px;
  position: relative;
  text-indent: -9999em;
  border-width: 4px;
  border-style: solid;
  border-color: rgba(182, 195, 204, 0.7);
  border-left-color: #3e87e8;
  transform: translateZ(0);
  animation: pxh-spinner 1.7s infinite linear;
  border-radius: 50%;
  width: 40px;
  height: 40px;
}

.pxh-spinner--small::after {
  animation-duration: 1.6s;
  border-width: 2px;
  height: 20px;
  width: 20px;
}

.pxh-spinner--large::after {
  animation-duration: 1.8s;
  border-width: 6px;
  height: 60px;
  width: 60px;
}

.pxh-spinner--inverted::after {
  border-color: rgba(66, 88, 102, 0.7);
  border-left-color: #3e87e8;
}

.pxh-spinner--finished::after {
  display: none;
}

@keyframes pxh-spinner {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

