// ** #THEME ** //

/// The following string will be replaced with the user's theme variables:
// ** THEME VARS GO HERE ** //

/*------------------------------------*\
    #settings.theme-default
\*------------------------------------*/

/// Define whether the drawer should appear on the left or right side of the browser window
/// @group pxh-settings
/// @type String
$pxh-drawer-side: null !default;

/// Define the side of the browser window opposite the drawer (e.g. 'right' if the drawer should appear on the left)
/// @group pxh-settings
/// @type String
$pxh-drawer-opposite-side: null !default;

@if $pxh-language-direction == 'rtl' {
  $pxh-drawer-side: right !default;
  $pxh-drawer-opposite-side: left !default;
} @else {
  $pxh-drawer-side: left !default;
  $pxh-drawer-opposite-side: right !default;
}




/// Path to web font
/// @group pxh-settings
/// @type String [default] - Webfont path
$webfont-path: '../fonts' !default;

/// Name to associate with web font
$webfont-name: 'GE Inspira Sans' !default;

/// File name of regular weight web font
$webfont-file-name-regular: GEInspiraSans !default;

/// File name of bold weight web font
$webfont-file-name-bold: GEInspiraSans-Bold !default;

/// File name of italic web font 
$webfont-file-name-italic: GEInspiraSans-Italic !default;

/// File name of bold italic web font
$webfont-file-name-bold-italic: GEInspiraSans-BoldItalic !default;




/// List of media query breakpoints
/// @group pxh-settings
$mq-breakpoints: (
  md:  768px,
  lg: 1024px,
) !default;




/// Path to Font Awesome fonts
/// @group pxh-settings
/// @type String
//$fa-font-path: '//dzlpbrbc7yvq0.cloudfront.net/libs/font-awesome/4.4.0/fonts' !default;
$fa-font-path: '../fonts' !default;



/// <html> and <body> elements
/// @group pxh-base
/// Base background color
$pxh-base-bg-color: $gray8 !default;

/// Base text color
$pxh-base-text-color: $black !default;

/// Base font family
$pxh-base-font-family: 'GE Inspira Sans', 'Helvetica', 'Arial', sans-serif !default;



/// <hr> element
/// @group pxh-hr
/// Horizontal rule color
$pxh-hr-color: $gray6 !default;



/// <a> element
/// @group pxh-link 
/// Default hyperlink text color
$pxh-link-text-color: $primary-blue !default;

/// Hyperlink text color on hover
$pxh-link-text-color-hover: $pxh-link-text-color !default;

/// Default hyperlink text decoration 
$pxh-link-text-decoration: underline !default;

/// Hyperlink text decoration on hover
$pxh-link-text-decoration-hover: none !default;




