/*------------------------------------*\
    #tools.pxh-animations
\*------------------------------------*/

@mixin in-state-opacity {
  opacity: 1;
}

@mixin out-state-opacity {
  opacity: 0;
}

@mixin out-state-scale {
  margin: 0;
  padding: 0;
  font-size: 0;
}

@mixin out-state {
  @include out-state-scale;
  @include out-state-opacity;
}

// mixin for embedding common animation declarations across pxh-navigation elements
@mixin pxh-animation($name) {
  animation-name: $name;
  animation-duration: 500ms;
  animation-timing-function: ease-in-out;
  animation-iteration-count: 1;
}

$mega-animate-default-config: (
  font-size-in: $pxh-base-font-size,
  margin-in: 0,
  padding-in: 0,
  border-in: 0,
  height-in: auto,
  opacity-in: 1,
  animation-name-in: animate-in,
  font-size-out: 0,
  margin-out: 0,
  padding-out: 0,
  border-out: 0,
  height-out: auto,
  opacity-out: 0,
  animation-name-out: animate-out,
  animation-duration: 500ms,
  block-class: default
);

@mixin mega-keyframes($config: $mega-animate-default-config) {
  @keyframes #{map-get($config, block-class)}--#{map-get($config, animation-name-in)} {
    0% {
      font-size: map-get($config, font-size-out);
      margin: map-get($config, margin-out);
      border: map-get($config, border-out);
      padding: map-get($config, padding-out);
      height: map-get($config, height-out);
      opacity: map-get($config, opacity-out);
    }
    33% {
      font-size: map-get($config, font-size-in);
      margin: map-get($config, margin-in);
      border: map-get($config, border-in);
      padding: map-get($config, padding-in);
      height: map-get($config, height-in);
      opacity: map-get($config, opacity-out);
    }
    100% {
      font-size: map-get($config, font-size-in);
      margin: map-get($config, margin-in);
      border: map-get($config, border-in);
      padding: map-get($config, padding-in);
      height: map-get($config, height-in);
      opacity: map-get($config, opacity-in);
    }
  }

  @keyframes #{map-get($config, block-class)}--#{map-get($config, animation-name-out)} {
    0% {
      font-size: map-get($config, font-size-in);
      margin: map-get($config, margin-in);
      border: map-get($config, border-in);
      padding: map-get($config, padding-in);
      height: map-get($config, height-in);
      opacity: map-get($config, opacity-in);
    }
    66% {
      font-size: map-get($config, font-size-in);
      margin: map-get($config, margin-in);
      border: map-get($config, border-in);
      padding: map-get($config, padding-in);
      height: map-get($config, height-in);
      opacity: map-get($config, opacity-out);
    }
    100% {
      font-size: map-get($config, font-size-out);
      margin: map-get($config, margin-out);
      border: map-get($config, border-out);
      padding: map-get($config, padding-out);
      height: map-get($config, height-out);
      opacity: map-get($config, opacity-out);
    }
  }
}

@mixin mega-animate($config) {
  font-size: map-get($config, font-size-in);
  margin: map-get($config, margin-in);
  border: map-get($config, border-in);
  padding: map-get($config, padding-in);
  height: map-get($config, height-in);
  opacity: map-get($config, opacity-in);
  &--animate-in {
    font-size: map-get($config, font-size-in);
    margin: map-get($config, margin-in);
    border: map-get($config, border-in);
    padding: map-get($config, padding-in);
    height: map-get($config, height-in);
    opacity: map-get($config, opacity-in);
    animation-name: #{map-get($config, block-class)}--#{map-get($config, animation-name-in)};
    animation-duration: map-get($config, animation-duration);
    animation-timing-function: ease-in-out;
    animation-fill-mode: forwards;
    animation-iteration-count: 1;
  }
  &--animate-out {
    font-size: map-get($config, font-size-out);
    margin: map-get($config, margin-out);
    border: map-get($config, border-out);
    padding: map-get($config, padding-out);
    height: map-get($config, height-out);
    opacity: map-get($config, opacity-out);
    animation-name: #{map-get($config, block-class)}--#{map-get($config, animation-name-out)};
    animation-duration: map-get($config, animation-duration);
    animation-timing-function: ease-in-out;
    animation-fill-mode: forwards;
    animation-iteration-count: 1;
  }
  &--hidden {
    font-size: map-get($config, font-size-out);
    margin: map-get($config, margin-out);
    border: map-get($config, border-out);
    padding: map-get($config, padding-out);
    height: map-get($config, height-out);
    opacity: map-get($config, opacity-out);
  }
  &--visible {
    font-size: map-get($config, font-size-out);
    margin: map-get($config, margin-out);
    border: map-get($config, border-out);
    padding: map-get($config, padding-out);
    height: map-get($config, height-out);
    opacity: map-get($config, opacity-out);
  }
}

/*------------------------------------*\
    #tools.pxh-mixins
\*------------------------------------*/

@mixin pxh-fancy-scrollbar {
  scrollbar-base-color: $pxh-scrollbar-base-color;
  scrollbar-arrow-color: $pxh-scrollbar-arrow-color;
  scrollbar-track-color: $pxh-scrollbar-track-color;
  scrollbar-face-color: $pxh-scrollbar-face-color;
  -webkit-overflow-scrolling: touch;

  &::-webkit-scrollbar {
    width: $pxh-scrollbar-width;
  }

  &::-webkit-scrollbar-track {
      background-color: $pxh-scrollbar-track-color;
      border: 2px solid $pxh-scrollbar-track-color;
  }

  &::-webkit-scrollbar-thumb {
    border-style: solid;
    border-width: $pxh-scrollbar-thumb-border-width;
    border-color: $pxh-scrollbar-thumb-border-color;
    background-color: $pxh-scrollbar-thumb-color;
  }
}

@mixin pxh-override-px-theme-text-color($class-to-chain, $link-color: $pxh-navigation-link-text-color, $hover-color: $pxh-navigation-link-text-color-hover) {
  // resigned to using !important instead of chained identical selectors because it takes so many darn nested selectors to override polymer's a:not([style-scope]):not(.style-scope):link
  color: $link-color !important;

  &:link {
    color: $link-color !important;
  }

  &:visited {
    color: $link-color !important;
  }

  &:hover {
    color: $hover-color !important;
  }

  &:active {
    color: $hover-color !important;
  }
}

@mixin pxh-override-px-theme-bg-color($class-to-chain, $link-color: $pxh-link-text-color, $hover-color: $link-color) {
  // resigned to using !important instead of chained identical selectors because it takes so many darn nested selectors to override polymer's a:not([style-scope]):not(.style-scope):link
  background-color: $link-color !important;

  &:link {
    background-color: $link-color !important;
  }

  &:visited {
    background-color: $link-color !important;
  }

  &:hover {
    background-color: $hover-color !important;
  }
  
  &:active {
    background-color: $hover-color !important;
  }
}


// ** #ELEMENTS ** //

/*------------------------------------*\
    #elements.body
\*------------------------------------*/

$pxh-language-direction : 'ltr' !default; // 'ltr' OR 'rtl'

html {
  font-size: ($pxh-base-font-size / 16px) * 1em;
  line-height: $pxh-base-line-height / $pxh-base-font-size;
  background-color: $pxh-base-bg-color;
  color: $pxh-base-text-color;
}

html, body {
  @if $pxh-language-direction == 'rtl' {
    direction: rtl;
  }
  font-family: $pxh-base-font-family;
  height: 100%;
  width: 100%;
  overflow: hidden;
}

body {
  min-height: 100%;
}

/*------------------------------------*\
    #elements.links
\*------------------------------------*/

a {
  color: $pxh-link-text-color;
  text-decoration: $pxh-link-text-decoration;
  &:hover {
    color: $pxh-link-text-color-hover;
    text-decoration: $pxh-link-text-decoration-hover;
  }
}

/*------------------------------------*\
    #elements.hr
\*------------------------------------*/

hr {
    height: 1px;
    color: $pxh-hr-color;
    background: $pxh-hr-color;
    font-size: 0;
    border: 0;
    margin-top: $pxh-base-spacing-unit;
    margin-bottom: $pxh-base-spacing-unit;
}


// ** #OBJECTS ** //

// ** #COMPONENTS ** //

/*------------------------------------*\
    #components.pxh-badge
\*------------------------------------*/

@mixin pxh-badge--in-state-scale {
  padding: 2px 4px 0px 4px;
  font-size: $pxh-badge-font-size;
}

@mixin pxh-badge--in-state-opacity {
  @include in-state-opacity;
}

@mixin pxh-badge--in-state {
  @include pxh-badge--in-state-opacity;
  @include pxh-badge--in-state-scale;
}

@mixin pxh-badge--out-state-opacity {
  @include out-state-opacity;
}

@mixin pxh-badge--out-state-scale {
  @include out-state-scale;
  border: 0;
}

@mixin pxh-badge--out-state {
  @include pxh-badge--out-state-opacity;
  @include pxh-badge--out-state-scale;
}

// no keyframes because we're not using animation classes yet

.pxh-badge {
  @include pxh-badge--in-state;
  text-align: center;
  background-color: $pxh-badge-bg-color;
  display: inline-block;
  font-weight: normal;
  color: $pxh-badge-text-color;
  line-height: 1;
  border-style: solid;
  border-width: $pxh-badge-border-width;
  border-color: $pxh-badge-border-color;
  border-radius: 6px;
  text-decoration: none;
  transition: opacity 250ms ease-in-out;
  transition-delay: 250ms;

  &--navigation {
    background-color: $pxh-badge-bg-color-navigation;
    border-color: $pxh-badge-border-color-navigation;
  }

  &--primary {
    background-color: $pxh-badge-bg-color-primary;
    border-color: $pxh-badge-border-color-primary;
  }

  &--important {
    background-color: $pxh-badge-bg-color-important;
    border-color: $pxh-badge-border-color-important;
  }

  &--warning {
    background-color: $pxh-badge-bg-color-warning;
    border-color: $pxh-badge-border-color-warning;
  }

  &--error {
    background-color: $pxh-badge-bg-color-error;
    border-color: $pxh-badge-border-color-error;
  }

  &--info {
    background-color: $pxh-badge-bg-color-info;
    border-color: $pxh-badge-border-color-info;
  }

  &--healthy-black {
    background-color: $pxh-badge-bg-color-healthy-black;
    border-color: $pxh-badge-border-color-healthy-black;
  }

  &--healthy-white {
    background-color: $pxh-badge-bg-color-healthy-white;
    border-color: $pxh-badge-border-color-healthy-white;
    color: $pxh-badge-text-color-inverse;
  }

  &--truncated {
    max-width: $pxh-base-spacing-unit * 3;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }

  &--hidden {
    @include pxh-badge--out-state;
  }

  @include mq($from: lg) {
    &--visible\@lg {
      @include pxh-badge--in-state;
    }
  }

  @include mq($from: md) {
    &--hidden\@md {
      @include pxh-badge--out-state;
    }
  }
}

/*------------------------------------*\
    #components.pxh-closer
\*------------------------------------*/

.pxh-closer {
  height: 100%;
  width: 100%;
  position: absolute;
  background-color: transparent; // $black
  opacity: 0;
  &--invisible {
    opacity: 0;
  }
  &--opaque {
    opacity: 1;
  }
  &--transparent {
    opacity: 0.5
  }
}

/*------------------------------------*\
    #components.pxh-drawer
\*------------------------------------*/

@keyframes pxh-drawer--animate-in {
  0% {
    #{$pxh-drawer-side}: -$pxh-drawer-width-wide - ($pxh-drawer-margin / 2);
  }
  85% {
    #{$pxh-drawer-side}: 0;
  }
  100% {
    #{$pxh-drawer-side}: 0;
  }
}

@keyframes pxh-drawer--animate-out {
  0% {
    #{$pxh-drawer-side}: 0;
  }
  85% {
    #{$pxh-drawer-side}: -$pxh-drawer-width-wide - ($pxh-drawer-margin / 2);
  }
  100% {
    #{$pxh-drawer-side}: -$pxh-drawer-width-wide - ($pxh-drawer-margin / 2);
  }
}

@keyframes pxh-drawer--animate-wide {
  0% {
    width: $pxh-drawer-width-narrow;
  }
  33% {
    width: $pxh-drawer-width-wide;
  }
  100% {
    width: $pxh-drawer-width-wide;
  }
}

@keyframes pxh-drawer--animate-out-wide {
  0% {
    #{$pxh-drawer-side}: 0;
    // transform: translateX(0);
    width: $pxh-drawer-width-narrow;
  }
  33% {
    // transform: translateX(-$pxh-drawer-width-wide);
    #{$pxh-drawer-side}: -$pxh-drawer-width-wide - ($pxh-drawer-margin / 2);
    width: $pxh-drawer-width-narrow;
  }
  66% {
    // transform: translateX(-$pxh-drawer-width-wide);
    #{$pxh-drawer-side}: -$pxh-drawer-width-wide - ($pxh-drawer-margin / 2);
    width: $pxh-drawer-width-wide;
  }
  100% {
    // transform: translateX(-$pxh-drawer-width-wide);
    #{$pxh-drawer-side}: -$pxh-drawer-width-wide;
    width: $pxh-drawer-width-wide;
  }
}

@keyframes pxh-drawer--animate-narrow {
  0% {
    width: $pxh-drawer-width-wide;
  }
  33% {
    width: $pxh-drawer-width-wide;
  }
  66% {
    width: $pxh-drawer-width-narrow;
  }
  100% {
    width: $pxh-drawer-width-narrow;
  }
}

// Set the default styles for the wide/open state of the drawer (as seen on mobile).
.pxh-drawer {
  z-index: 750;
  position: fixed;
  top: 0;
  #{$pxh-drawer-side}: 0;
  width: $pxh-drawer-width-wide;
  height: 100%;
  // disabled until we figure out how to add a border without reducing the logo width
  // border-style: solid;
  // border-width: $pxh-drawer-border-width;
  // border-color: $pxh-drawer-border-color;
  background-color: $pxh-drawer-bg-color;
  box-shadow: $pxh-drawer-box-shadow;

  &--image {
    // We don't need this class yet but we anticipate other people needing it in the future
  }

  &--animate-in {
    animation-timing-function: ease-in-out;
    animation-fill-mode: forwards;
    animation-duration: 250ms;
    animation-name: pxh-drawer--animate-in;
  }
  
  &--animate-out {
    animation-timing-function: ease-in-out;
    animation-fill-mode: forwards;
    animation-duration: 250ms;
    animation-name: pxh-drawer--animate-out;
  }

  &--animate-narrow {
    animation-timing-function: ease-in-out;
    animation-fill-mode: forwards;
    animation-duration: 1000ms;
    animation-name: pxh-drawer--animate-narrow;
  }

  &--animate-wide {
    animation-timing-function: ease-in-out;
    animation-fill-mode: forwards;
    animation-duration: 500ms;
    animation-name: pxh-drawer--animate-wide;
  }

  &--animate-out-wide {
    animation-timing-function: ease-in-out;
    animation-fill-mode: forwards;
    animation-duration: 1000ms;
    animation-name: pxh-drawer--animate-out-wide;
  }

  // Keep the inactive drawer hidden off-screen until the @md (tablet) breakpoint.
  @include mq($until: md) {
    &--hidden-until\@md {
      #{$pxh-drawer-side}: -$pxh-drawer-width-wide - ($pxh-drawer-margin / 2);
    }
  }

  // From the @md (tablet) breakpoint and wider,
  // the inactive drawer is displayed in its narrow state.
  // Remove this class to display the drawer in its wide state.
  @include mq($from: md) {
    &--narrow\@md {
      width: $pxh-drawer-width-narrow;
    }
  }

  @include mq($from: lg) {
    &--wide\@lg {
      width: $pxh-drawer-width-wide;
    }
  }
}

.pxh-drawer-toggle {
  float: $pxh-drawer-side;

  &__link {
    display: block;
    float: $pxh-drawer-side;
    width: $pxh-drawer-margin * 3;
    height: $pxh-drawer-margin * 3;
    padding: $pxh-drawer-margin;
    color: $pxh-drawer-toggle-icon-color;
    &:link,
    &:visited {
      color: $pxh-drawer-toggle-icon-color;
    }
  }

  &__icon {
    fill: $pxh-drawer-toggle-icon-color;
  }
}

/*------------------------------------*\
    #pxh-drawer-header
\*------------------------------------*/

@mixin pxh-drawer-header__link--in-state-opacity {
  @include in-state-opacity;
}

@mixin pxh-drawer-header__link--in-state-scale {
  padding-top: $pxh-drawer-margin;
  padding-bottom: 0;
  margin-top: 0;
  margin-bottom: 0;
  width: $pxh-drawer-width-wide - ($pxh-drawer-margin * 3) - 1px;
  font-size: $pxh-drawer-header-link-font-size;
}

@mixin pxh-drawer-header__link--in-state {
  @include pxh-drawer-header__link--in-state-scale;
  @include pxh-drawer-header__link--in-state-opacity;
}

@keyframes pxh-drawer-header__link--animate-in {
  0% {
    @include out-state;
  }
  33% {
    @include pxh-drawer-header__link--in-state-scale;
    @include out-state-opacity;
  }
  100% {
    @include pxh-drawer-header__link--in-state;
  }
}

@keyframes pxh-drawer-header__link--animate-out {
  0% {
    @include pxh-drawer-header__link--in-state;
  }
  33% {
    @include out-state-opacity;
    @include pxh-drawer-header__link--in-state-scale;
  }
  34% {
    @include out-state-opacity;
    @include pxh-drawer-header__link--in-state-scale;
  }
  100% {
    @include out-state;
  }
}

.pxh-drawer-header {
  float: $pxh-drawer-side;
  text-align: $pxh-drawer-side;
  margin: 0;
  padding: 0;
  font-weight: normal;

  &__link {
    @include pxh-drawer-header__link--in-state;
    display: block;
    text-decoration: none;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;

    &--animate-in {
      animation-fill-mode: forwards;
      animation-duration: 1000ms;
      animation-name: pxh-drawer-header__link--animate-in;
    }

    &--animate-out {
      animation-fill-mode: forwards;
      animation-duration: 1000ms;
      animation-name: pxh-drawer-header__link--animate-out;
    }

    &--image {
      display: block;
      background-image: $pxh-drawer-header-image;
      background-repeat: no-repeat;
      background-position: #{$pxh-drawer-side} center;
      background-size: contain;
      height: $pxh-drawer-margin * 3;
      width: $pxh-drawer-width-wide - ($pxh-drawer-margin * 3);
      text-indent: -9999px;
      font-size: 1px;
    }

    @include mq($from: md) {
      &--narrow\@md {
        @include out-state;
      }
    }

    @include mq($from: md) {
      &--wide\@md {
        @include pxh-drawer-header__link--in-state;
      }
    }

    @include mq($from: lg) {
      &--wide\@lg {
        @include pxh-drawer-header__link--in-state;
      }
    }
  }
}

// chain to override px-theme
.pxh-drawer-header__link {
  @include pxh-override-px-theme-text-color('pxh-drawer-header__link');
} 

/*------------------------------------*\
    #components.pxh-error
\*------------------------------------*/

.pxh-error {
  width: 100%;
  max-width: 1200px;
  text-align: center;
  margin: $pxh-error-margin-top 0 0 0;
  padding: $pxh-error-padding-top $pxh-base-spacing-unit ($pxh-base-spacing-unit * 8) $pxh-base-spacing-unit;
}

@include mq($from: md) {
  .pxh-error\@md {
    margin-left: auto;
    margin-right: auto;
    width: 90%;
  }
}

@include mq($from: lg) {
  .pxh-error\@lg {
    margin-left: auto;
    margin-right: auto;
    width: 70%;
  }
}

.pxh-error__icon {
  font-size: $pxh-error-icon-font-size;
  color: $pxh-error-icon-color;
  line-height: 1;
}

.pxh-error__title {
  font-size: $pxh-error-title-font-size;
  font-weight: bold;
  color: $pxh-error-title-text-color;
