/*------------------------------------*\
    #components.pxh-notification
\*------------------------------------*/

@mixin pxh-toast {
  display: flex;
  // @if $pxh-language-direction == 'rtl' { // handled by direction: rtl on body element
  //   flex-direction: row-reverse;
  // }
  pointer-events: auto;
  overflow: hidden;
  position: relative;
  width: 100%;
  color: $pxh-toast-text-color;
  background: $pxh-toast-bg-color;
  &--expanded {
    height: auto;
  }
}

@mixin pxh-toast__icon {
  text-align: center;
  flex-shrink: 0;
  align-self: stretch;
  min-height: $pxh-toast-height;
}

@mixin pxh-toast__text {
  text-align: $pxh-drawer-side;
  flex-grow: 2;
  margin: auto $pxh-toast-margin auto $pxh-toast-margin;
  padding: $pxh-toast-margin 0 0 0;
  padding-#{$pxh-drawer-side}: ($pxh-toast-margin * 2);
  overflow: hidden;
  position: relative;
  line-height: 1.2;
}

@mixin pxh-toast__more {
  position: absolute;
  top: $pxh-toast-margin * 7;
  width: 100%;
  height: $pxh-toast-margin * 7;
  padding: ($pxh-toast-margin * 3) - 2px 0 $pxh-toast-margin 0;
  background: $pxh-toast-more-bg;
  text-align: center;
  &--expanded {
    position: relative;
    top: 0;
    padding-bottom: $pxh-toast-margin - 2px;
    padding-left: $pxh-toast-margin * 2;
    height: auto;
    background: transparent;
  }
}

@mixin pxh-toast__more-button {
  color: $pxh-toast-more-button-text-color;
  background-color: $pxh-toast-more-button-bg-color;
  border-style: solid;
  border-width: $pxh-toast-more-button-border-width;
  border-color: $pxh-toast-more-button-border-color;
  border-radius: 100%;
  height: $pxh-toast-margin * 4;
  width: $pxh-toast-margin * 4;
  padding: 2px 0 1px 0;
  display: inline-block;
  text-decoration: none;
  text-transform: lowercase;
  font-size: $pxh-toast-more-button-font-size;
  &:hover {
    color: $pxh-toast-more-button-text-color-hover;
    background-color: $pxh-toast-more-button-bg-color-hover;
  }
}

@mixin pxh-toast__action {
  flex-shrink: 0;
  text-align: center;
  margin: ($pxh-toast-margin * 3) ($pxh-toast-margin * 2) 0 ($pxh-toast-margin * 2);
}

@mixin pxh-toast__button {
  display: block;
  overflow: hidden;
  text-decoration: none;
  border-style: solid;
  border-width: $pxh-toast-button-border-width;
  border-color: $pxh-toast-button-border-color;
  padding: ($pxh-base-spacing-unit / 2);
  background-color: $pxh-toast-button-bg-color;
  color: $pxh-toast-button-text-color;
  border-radius: 2px;
  white-space: nowrap;
  text-overflow: ellipsis;
  &:hover {
    color: $pxh-toast-button-text-color-hover;
    background-color: $pxh-toast-button-bg-color-hover;
  }
}

@mixin pxh-toast__timestamp {
  margin-left: $pxh-toast-margin;
  color: $pxh-toast-timestamp-text-color;
  flex-shrink: 0;
  text-align: $pxh-drawer-opposite-side;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}

@mixin pxh-toast__dismiss {
  text-align: center;
  font-size: $pxh-toast-dismiss-font-size;
  flex-basis: auto;
  width: $pxh-toast-margin * 8;
  flex-shrink: 0;
}

@mixin pxh-toast__dismiss-button {
  color: $pxh-toast-dismiss-button-icon-color;
  text-decoration: none;
  display: block;
  padding: ($pxh-toast-margin * 5) ($pxh-toast-margin * 2) 0 ($pxh-toast-margin * 2);
  height: $pxh-toast-height;
  &:hover {
    color: $pxh-toast-dismiss-button-icon-color-hover;
  }
}

@mixin pxh-toast--in-state-scale {
  height: $pxh-toast-height;
  margin-bottom: $pxh-toast-margin;
  font-size: $pxh-toast-font-size;
}

@mixin pxh-toast--in-state-opacity {
  @include in-state-opacity;
}

@mixin pxh-toast--out-state-scale {
    @include out-state-scale;
    height: 0;
}

@mixin pxh-toast--in-state {
  @include pxh-toast--in-state-opacity;
  @include pxh-toast--in-state-scale;
}

@mixin pxh-toast--out-state {
  @include pxh-toast--out-state-scale;
  @include out-state-opacity;
}

@mixin pxh-notification--in-state-scale {
  height: $pxh-toast-height;
  font-size: $pxh-notification-font-size;
  border-style: solid;
  border-width: $pxh-notification-border-width;
  border-color: $pxh-notification-border-color;
}

@mixin pxh-notification--in-state-opacity {
  @include in-state-opacity;
}

@mixin pxh-notification--out-state-scale {
    @include out-state-scale;
    height: 0;
}

@mixin pxh-notification--in-state {
  @include pxh-notification--in-state-opacity;
  @include pxh-notification--in-state-scale;
}

@mixin pxh-notification--out-state {
  @include pxh-notification--out-state-scale;
  @include out-state-opacity;
}

@keyframes pxh-notification--animate-in {
  0% {
    @include pxh-notification--out-state;
  }
  33% {
    @include pxh-notification--in-state-scale;
    @include out-state-opacity;
  }
  66% {
    @include pxh-notification--in-state;
  }
  100% {
    @include pxh-notification--in-state;
  }
}

@keyframes pxh-notification--animate-out {
  0% {
    @include pxh-notification--in-state;
  }
  33% {
    @include pxh-notification--in-state-scale;
    @include out-state-opacity;
  }
  66% {
    @include pxh-notification--out-state;
  }
  100% {
    @include pxh-notification--out-state;
  }
}

.pxh-notifications {
  z-index: 800;
  position: fixed;
  bottom: ($pxh-drawer-margin * 3) - 4px;
  left: $pxh-notification-margin;
  right: $pxh-notification-margin;
  max-width: $pxh-base-spacing-unit * 32;
  border-radius: 2px;
  background-color: $pxh-notifications-bg-color;
  color: $pxh-notifications-text-color;
  border-style: solid;
  border-width: $pxh-notifications-border-width;
  border-color: $pxh-notifications-border-color;
  display: none;

  @include mq($from: md) {
    &\@md {
      width: $pxh-base-spacing-unit * 32;
    }

    &--narrow\@md {
      #{$pxh-drawer-side}: $pxh-notification-margin;
      #{$pxh-drawer-opposite-side}: auto;
      bottom: ($pxh-drawer-margin * 3) + 6px;
    }
  }

  @include mq($from: lg) {
    &--wide\@lg {
      #{$pxh-drawer-side}: $pxh-notification-margin;
      #{$pxh-drawer-opposite-side}: auto;
      bottom: ($pxh-drawer-margin * 3) - 4px;
    }
  }

  &--visible {
    display: block;
  }

  &__header {
    height: ($pxh-base-spacing-unit * 2) + (($pxh-base-spacing-unit / 3) * 2); // magic number -- fix this
    display: flex;
    // @if $pxh-language-direction == 'rtl' { // handled by direction: rtl on body element
    //   flex-direction: row-reverse;
    // }
    border-style: solid;
    border-width: $pxh-notifications-header-border-width;
    border-color: $pxh-notifications-header-border-color;
  }

  &__title {
    padding-top: $pxh-notification-margin * 2;
    flex-grow: 1;
    text-align: center;
    text-transform: uppercase;
    font-weight: bold;
  }

  &__action {
    min-width: $pxh-notification-margin * 16;
    text-align: $pxh-drawer-side;
    padding-#{$pxh-drawer-side}: $pxh-notification-margin * 2;
    padding-top: $pxh-notification-margin * 2;
  }

  &__link {
    @include pxh-override-px-theme-text-color('pxh-notifications__link', $pxh-notifications-link-text-color, $pxh-notifications-link-text-color-hover);
    color: $pxh-notifications-link-text-color;
    text-decoration: none;

    &:hover {
      color: $pxh-notifications-link-text-color-hover
    }
  }

  &__clear {
    min-width: $pxh-notification-margin * 16;
    color: $pxh-notifications-clear-text-color;
    text-decoration: none;
    text-align: $pxh-drawer-opposite-side;
    padding-right: $pxh-notification-margin * 2;
    padding-left: $pxh-notification-margin * 2;
    padding-top: $pxh-notification-margin * 2;

    a {
      color: $pxh-notifications-clear-text-color;
      text-decoration: none;

      &:hover {
        color: $pxh-notifications-clear-text-color-hover;
      }
    }
  }

  &__list {
    @include pxh-fancy-scrollbar;
    position: relative;
    max-height: ($pxh-notification-height * 3) + ($pxh-notification-height / 2);
    min-height: $pxh-notification-height;
    -webkit-overflow-scrolling: touch;
    overflow-y: auto;
    @include mq($from: md) {
      &\@md {
      max-height: ($pxh-notification-height * $pxh-notification-max-count) + ($pxh-notification-height / 2);
      }
    }
  }

  &__empty {
    position: absolute;
    bottom: 0;
    height: $pxh-notification-height;
    z-index: 850;
    font-style: italic;
    color: $pxh-notifications-empty-text-color;
    width: 100%;
    text-align: center;
    padding-top: $pxh-notification-height / 3;
  }
}

.pxh-notification {
  @include pxh-toast;
  @include pxh-notification--in-state;
  z-index: 900;

  &--animate-in {
    animation-fill-mode: forwards;
    animation-duration: 1000ms;
    animation-name: pxh-notification--animate-in;
  }

  &--animate-out {
    animation-fill-mode: forwards;
    animation-duration: 1000ms;
    animation-name: pxh-notification--animate-out;
  }

  &__icon {
    @include pxh-toast__icon;
    padding-top: $pxh-toast-margin * 4;
    padding-#{$pxh-drawer-side}: $pxh-toast-margin;
    flex-basis: $pxh-toast-margin * 8;
    font-size: $pxh-notification-icon-font-size;

    &--green {
      color: $pxh-notification-icon-color-green;
    }

    &--blue {
      color: $pxh-notification-icon-color-blue;
    }

    &--orange {
      color: $pxh-notification-icon-color-orange;
    }

    &--red {
      color: $pxh-notification-icon-color-red;
    }
  }

  &__text {
    @include pxh-toast__text;
  }

  &__link {
    @include pxh-override-px-theme-text-color('pxh-notification__link', $pxh-notification-link-text-color, $pxh-notification-link-text-color-hover);
    text-decoration: none;
    color: $pxh-notification-link-text-color;

    &:hover {
      color: $pxh-notification-link-text-color-hover;
    }
  }

  &__more {
    @include pxh-toast__more;
  }

  &__more-button {
    @include pxh-toast__more-button;
    @include pxh-override-px-theme-text-color('pxh-notification__more-button'$pxh-notification-more-button-text-color, $pxh-notification-more-button-text-color-hover);
    @include pxh-override-px-theme-bg-color('pxh-notification__more-button', $pxh-notification-more-button-bg-color, $pxh-notification-more-button-bg-color-hover);
  }

  &__timestamp {
    @include pxh-toast__timestamp;
    padding-top: $pxh-toast-margin * 5.5;
    max-width: $pxh-toast-height * 1.5;
  }

  &__dismiss {
    @include pxh-toast__dismiss;
  }

  &__dismiss-button {
    @include pxh-toast__dismiss-button;
    color: $pxh-notification-dismiss-button-icon-color;
    @include pxh-override-px-theme-text-color('pxh-notification__dismiss-button', $pxh-notification-dismiss-button-icon-color, $pxh-notification-dismiss-button-icon-color-hover);
  }
}

/*------------------------------------*\
    #components.pxh-overlay
\*------------------------------------*/

// Base styles for the overlay to display when
// pxh-drawer is toggled open on mobile and tablet.
.pxh-overlay {
  z-index: 500;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: auto;
  background-color: $pxh-overlay-bg-color;
  transition: all 200ms ease-in-out;
  transition-delay: 100ms;
}

// Modifier class that hides the overlay.
.pxh-overlay--hidden {
  pointer-events: none;
  background-color: rgba(0, 0, 0, 0);
}

// From the @lg (desktop) breakpoint and wider,
// prevent the overlay from being shown at all.
@include mq($from: lg) {
  .pxh-overlay--hidden\@lg {
    pointer-events: none;
    background-color: rgba(0, 0, 0, 0);
  }
}

/*------------------------------------*\
    #components.pxh-spinner
\*------------------------------------*/

.pxh-spinner {
  text-align: center;
  overflow: hidden;
  &, &--margin {
    margin: ($pxh-base-spacing-unit * 4) 0 ($pxh-base-spacing-unit * 4) 0;

  }
  &--no-margin {
    margin: 0;
  }
  &::after {
    content: ' ';
    display: inline-block;
    font-size: 1px;
    position: relative;
    text-indent: -9999em;
    border-width: 4px;
    border-style: solid;
    border-color: $pxh-spinner-base-color;
    border-left-color: $pxh-spinner-accent-color;
    transform: translateZ(0);
    animation: pxh-spinner 1.7s infinite linear;
    border-radius: 50%;
    width: ($pxh-base-spacing-unit * 2) + (($pxh-base-spacing-unit / 3) * 2);
    height: ($pxh-base-spacing-unit * 2) + (($pxh-base-spacing-unit / 3) * 2);
  }
  &--small::after {
    animation-duration: 1.6s;
    border-width: 2px;
    height: $pxh-base-spacing-unit + ($pxh-base-spacing-unit / 3);
    width: $pxh-base-spacing-unit + ($pxh-base-spacing-unit / 3);
  }
  &--large::after {
    animation-duration: 1.8s;
    border-width: 6px;
    height: $pxh-base-spacing-unit * 4;
    width: $pxh-base-spacing-unit * 4;
  }
  &--inverted::after {
    border-color: $pxh-spinner-base-color-inverted;
    border-left-color: $pxh-spinner-accent-color-inverted;
  }
  &--finished::after {
    display: none;
  }
}

@keyframes pxh-spinner {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

/*------------------------------------*\
    #components.pxh-toast
\*------------------------------------*/

.pxh-toasts {
  @include pxh-fancy-scrollbar;
  position: fixed;
  bottom: 0;
  right: $pxh-toast-margin;
  left: $pxh-toast-margin;
  -webkit-overflow-scrolling: touch;
  overflow-y: auto;
  z-index: 300;
  max-height: ($pxh-toast-height * 2) + ($pxh-toast-height / 2) + $pxh-toast-margin;

  @include mq($from: md) {
    &--narrow\@md {
      max-height: ($pxh-toast-height * $pxh-toast-max-count) + ($pxh-toast-height / 2) + ($pxh-toast-margin * ($pxh-toast-max-count - 1));
      top: $pxh-view-header-height + ($pxh-base-spacing-unit / 3);
      #{$pxh-drawer-side}: auto;
      width: $pxh-base-spacing-unit * 28;
      bottom: auto;
    }
  }

  @include mq($from: lg) {
    &--wide\@lg {
      #{$pxh-drawer-side}: auto;
      width: $pxh-base-spacing-unit * 32;
    }
  }
}

@keyframes pxh-toast--animate-in {
  0% {
    @include pxh-toast--out-state;
  }
  33% {
    @include pxh-toast--in-state-scale;
    @include out-state-opacity;
  }
  66% {
    @include pxh-toast--in-state;
  }
  100% {
    @include pxh-toast--in-state;
  }
}

@keyframes pxh-toast--animate-out {
  0% {
    @include pxh-toast--in-state;
  }
  33% {
    @include pxh-toast--in-state-scale;
    @include out-state-opacity;
  }
  66% {
    @include pxh-toast--out-state;
  }
  100% {
    @include pxh-toast--out-state;
  }
}

.pxh-toast {
  @include pxh-toast--in-state;
  @include pxh-toast;

  &--animate-in {
    animation-fill-mode: forwards;
    animation-duration: 1000ms;
    animation-name: pxh-toast--animate-in;
  }

  &--animate-out {
    animation-fill-mode: forwards;
    animation-duration: 1000ms;
    animation-name: pxh-toast--animate-out;
  }

  &__action {
    @include pxh-toast__action;
  }

  &__button {
    max-width: $pxh-toast-height * 1.75;
    @include pxh-toast__button;
    @include pxh-override-px-theme-text-color('pxh-toast__button', $pxh-toast-button-text-color);
    @include pxh-override-px-theme-bg-color('pxh-toast__button', $pxh-toast-button-bg-color, $pxh-toast-button-bg-color-hover);
  }

  &__icon {
    @include pxh-toast__icon;
    color: $pxh-toast-icon-color;
    padding-top: $pxh-toast-margin * 4;
    flex-basis: $pxh-toast-height;
    font-size: $pxh-toast-icon-font-size;

    &--green {
      background-color: $pxh-toast-icon-bg-color-green;
    }

    &--blue {
      background-color: $pxh-toast-icon-bg-color-blue;
    }

    &--orange {
      background-color: $pxh-toast-icon-bg-color-orange;
    }

    &--red {
      background-color: $pxh-toast-icon-bg-color-red;
    }
  }

  &__text {
    @include pxh-toast__text;
  }

  &__more {
    @include pxh-toast__more;
  }

  &__more-button {
    @include pxh-toast__more-button;
    @include pxh-override-px-theme-text-color('pxh-toast__more-button', $pxh-toast-more-button-text-color, $pxh-toast-more-button-text-color-hover);
    @include pxh-override-px-theme-bg-color('pxh-toast__more-button', $pxh-toast-more-button-bg-color, $pxh-toast-more-button-bg-color-hover);
  }

  &__dismiss {
    @include pxh-toast__dismiss;
  }

  &__dismiss-button {
    @include pxh-toast__dismiss-button;
    @include pxh-override-px-theme-text-color('pxh-toast__dismiss-button', $pxh-toast-dismiss-button-icon-color, $pxh-toast-dismiss-button-icon-color-hover);
  }
}

/*------------------------------------*\
    #components.pxh-view
\*------------------------------------*/

.pxh-wrapper {
  padding-top: $pxh-view-header-height;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

@keyframes pxh-view--animate-wide {
  0% {
    margin-#{$pxh-drawer-side}: $pxh-drawer-width-wide;
  }
  33% {
    margin-#{$pxh-drawer-side}: $pxh-drawer-width-wide;
  }
  66% {
    margin-#{$pxh-drawer-side}: $pxh-drawer-width-narrow;
  }
  100% {
    margin-#{$pxh-drawer-side}: $pxh-drawer-width-narrow;
  }
}

@keyframes pxh-view--animate-narrow {
  0% {
    margin-#{$pxh-drawer-side}: $pxh-drawer-width-narrow;
  }
  33% {
    margin-#{$pxh-drawer-side}: $pxh-drawer-width-wide;
  }
  100% {
    margin-#{$pxh-drawer-side}: $pxh-drawer-width-wide;
  }
}

@keyframes pxh-view--animate-full-to-wide {
  0% {
    margin-#{$pxh-drawer-side}: 0;
  }
  50% {
    margin-#{$pxh-drawer-side}: 0;
  }
  100% {
    margin-#{$pxh-drawer-side}: $pxh-drawer-width-narrow;
  }
}

@keyframes pxh-view--animate-full-to-narrow {
  0% {
    margin-#{$pxh-drawer-side}: 0;
  }
  50% {
    margin-#{$pxh-drawer-side}: 0;
  }
  100% {
    margin-#{$pxh-drawer-side}: $pxh-drawer-width-wide;
  }
}

.pxh-view {
  height: 100%;
  overflow: auto;
  background-color: $pxh-view-bg-color;
  -webkit-overflow-scrolling: touch;

  &--animate-wide {
    animation-timing-function: ease-in-out;
    animation-fill-mode: forwards;
    animation-duration: 1000ms;
    animation-name: pxh-view--animate-wide;
  }

  &--animate-narrow {
    animation-timing-function: ease-in-out;
    animation-fill-mode: forwards;
    animation-duration: 500ms;
    animation-name: pxh-view--animate-narrow;
  }

  &--animate-full-to-wide {
    animation-timing-function: ease-in-out;
    animation-fill-mode: forwards;
    animation-duration: 250ms;
    animation-name: pxh-view--animate-full-to-wide;
  }

  &--animate-full-to-narrow {
    animation-timing-function: ease-in-out;
    animation-fill-mode: forwards;
    animation-duration: 250ms;
    animation-name: pxh-view--animate-full-to-narrow;
  }
}

@each $color-slug, $color-variable in $pxh-view-bg-colors {
  .pxh-view--#{$color-slug} {
    background-color: $color-variable;
  }
}

// nest all responsive styles within a pxh-chromeful class (applied to the <body> element so they only apply if the chrome is present
// this violates our principle of immutability for class styles, but yolo
.pxh-chromeful {
  // From the @md (tablet) breakpoint and wider,
  // the view is displayed in its wide state, which accounts
  // for the width of the narrow state of the drawer on the left.
  @include mq($from: md) {
    .pxh-view--wide\@md {
      margin-#{$pxh-drawer-side}: $pxh-drawer-width-narrow;
    }
  }

  // From the @lg (desktop) breakpoint and wider,
  // add this class to display the view in its narrow state,
  // which accounts for the width of the wide state of the drawer.
  @include mq($from: lg) {
    .pxh-view--narrow\@lg {
      margin-#{$pxh-drawer-side}: $pxh-drawer-width-wide;
    }
  }

  @include mq($from: lg) {
    .pxh-view--wide\@lg {
      margin-#{$pxh-drawer-side}: $pxh-drawer-width-narrow;
    }
  }
}

/*------------------------------------*\
    #PXH-VIEW-HEADER
\*------------------------------------*/

@keyframes pxh-view-header--animate-wide {
  0% {
    #{$pxh-drawer-side}: $pxh-drawer-width-wide;
  }
  33% {
    #{$pxh-drawer-side}: $pxh-drawer-width-wide;
  }
  66% {
    #{$pxh-drawer-side}: $pxh-drawer-width-narrow;
  }
  100% {
    #{$pxh-drawer-side}: $pxh-drawer-width-narrow;
  }
}

@keyframes pxh-view-header--animate-narrow {
  0% {
    #{$pxh-drawer-side}: $pxh-drawer-width-narrow;
  }
  33% {
    #{$pxh-drawer-side}: $pxh-drawer-width-wide;
  }
  100% {
    #{$pxh-drawer-side}: $pxh-drawer-width-wide;
  }
}

@keyframes pxh-view-header--animate-full-to-wide {
  0% {
    #{$pxh-drawer-side}: 0;
  }
  50% {
    #{$pxh-drawer-side}: 0;
  }
  100% {
    #{$pxh-drawer-side}: $pxh-drawer-width-narrow;
  }
}

@keyframes pxh-view-header--animate-full-to-narrow {
  0% {
    #{$pxh-drawer-side}: 0;
  }
  50% {
    #{$pxh-drawer-side}: 0;
  }
  100% {
    #{$pxh-drawer-side}: $pxh-drawer-width-wide;
  }
}

.pxh-view-header {
  position: fixed;
  top: 0;
  #{$pxh-drawer-opposite-side}: 0;
  #{$pxh-drawer-side}: 0;
  background-color: $pxh-view-header-bg-color;
  border-style: solid;
  border-color: $pxh-view-header-border-color;
  border-width: $pxh-view-header-border-width;
  height: $pxh-view-header-height;
  z-index: 250;

  &--animate-wide {
    animation-timing-function: ease-in-out;
    animation-fill-mode: forwards;
    animation-duration: 1000ms;
    animation-name: pxh-view-header--animate-wide;
  }

  &--animate-narrow {
    animation-timing-function: ease-in-out;
    animation-fill-mode: forwards;
    animation-duration: 500ms;
    animation-name: pxh-view-header--animate-narrow;
  }

  &--animate-full-to-wide {
    animation-timing-function: ease-in-out;
    animation-fill-mode: forwards;
    animation-duration: 250ms;
    animation-name: pxh-view-header--animate-full-to-wide;
  }

  &--animate-full-to-narrow {
    animation-timing-function: ease-in-out;
    animation-fill-mode: forwards;
    animation-duration: 250ms;
    animation-name: pxh-view-header--animate-full-to-narrow;
  }
}

// nest all responsive styles within a pxh-chromeful class (applied to the <body> element so they only apply if the chrome is present
// this violates our principle of immutability for class styles, but yolo
.pxh-chromeful {
  // From the @md (tablet) breakpoint and wider,
  // the view header is displayed in its wide state, which accounts
  // for the width of the narrow state of the drawer on the left.
  @include mq($from: md) {
    .pxh-view-header--wide\@md {
      #{$pxh-drawer-side}: $pxh-drawer-width-narrow;
    }
  }

  @include mq($from: lg) {
    .pxh-view-header--wide\@lg {
      #{$pxh-drawer-side}: $pxh-drawer-width-narrow;
    }
  }

  @include mq($until: md) {
    .pxh-view-header--nudge-until\@md {
      padding-#{$pxh-drawer-side}: $pxh-drawer-width-narrow;
    }
  }

  // From the @lg (desktop) breakpoint and wider,
  // add this class to display the view header in its narrow state,
  // which accounts for the width of the wide state of the drawer.
  @include mq($from: lg) {
    .pxh-view-header--narrow\@lg {
      #{$pxh-drawer-side}: $pxh-drawer-width-wide;
    }
  }
}

.pxh-view-header__title {
  font-weight: normal;
  margin: 0;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  padding-top: $pxh-base-spacing-unit;
  text-align: center;
  font-size: $pxh-view-header-title-font-size;
}

.pxh-view-header__title-link.pxh-view-header__title-link.pxh-view-header__title-link  { // chained to override px-theme
  color: $pxh-view-header-title-link-text-color;
  text-decoration: none;
}

.pxh-view-header__title-link:link.pxh-view-header__title-link:link.pxh-view-header__title-link:link,
.pxh-view-header__title-link:visited.pxh-view-header__title-link:visited.pxh-view-header__title-link:visited { // chained to override px-theme
  color: $pxh-view-header-title-link-text-color;
}

/*------------------------------------*\
    #PXH-VIEW-HEADER-DRAWER-TOGGLE
\*------------------------------------*/

.pxh-view-header-drawer-toggle {
  position: fixed;
  top: 0;
  #{$pxh-drawer-side}: 0;
  opacity: 1;
  z-index: 500; // making the same as overlay to overcome IE10 clickthru bug with overlay
  transition: all 300ms ease-in-out;

  &__icon {
    fill: $pxh-view-header-drawer-toggle-icon-color;
  }

  // Hide the toggle button in the view-header. This class is
  // used even though the drawer covers the view-header toggle
  // button, because sometimes click events on the drawer toggle
  // are intercepted by the view-header toggle as well, resulting
  // in a bounce scenario.
  &--hidden {
    #{$pxh-drawer-side}: $pxh-drawer-width-narrow * -1;
    opacity: 0;  
  }

  &__link {
    display: block;
    height: $pxh-drawer-width-narrow;
    width: $pxh-drawer-width-narrow;
    padding-top: $pxh-drawer-margin;
    padding-#{$pxh-drawer-side}: $pxh-drawer-margin;
    text-decoration: none;
  }
}

// nest all responsive styles within a pxh-chromeful class (applied to the <body> element so they only apply if the chrome is present
// this violates our principle of immutability for class styles, but yolo
.pxh-chromeful {
  // From the @md (tablet) breakpoint and wider, hide the
  // toggle button in the view-header, as the toggle in the
  // narrow left drawer is persistently displayed instead.
  @include mq($from: md) {
    .pxh-view-header-drawer-toggle--hidden\@md {
        #{$pxh-drawer-side}: $pxh-drawer-width-narrow * -1;
        opacity: 0;
    }
  }
}


// ** #OVERRIDES ** //

/*------------------------------------*\
    #overrides.px-theme
\*------------------------------------*/

html {
  // sets color as default for application background:
  background-color: $pxh-base-bg-color !important;
  // prevents duplicate scroll bars on the viewport in addition to the pxh-view content area in IE:
  overflow: hidden !important;
}

body {
  margin-bottom: 0 !important;
}

/*------------------------------------*\
    #overrides.pxh-utilities
\*------------------------------------*/

// utility class to disable scrolling
@include mq($until: lg) {
  .pxh-disable-scroll-until\@lg {
    overflow: hidden;
    -webkit-overflow-scrolling: auto;
  }
}

.pxh-float-left {
  float: left;
}

.pxh-float-right {
  float: right;
}

.pxh-clear-left {
  clear: left;
}

.pxh-clear-right {
  clear: right;
}

.pxh-clear-both {
  clear: both;
}

.pxh-display-none {
  display: none;
}

.pxh-clearfix:after {
  content: "";
  display: table;
  clear: both;
}

/*------------------------------------*\
    #overrides.pxh-docs
\*------------------------------------*/

.pxh-docs {
  margin: $pxh-base-spacing-unit / 3;
  padding: $pxh-base-spacing-unit + ($pxh-base-spacing-unit / 3);
  background-color: $pxh-docs-bg-color;

  code {
    font-size: $pxh-base-font-size;
    background-color: $pxh-docs-code-bg-color;
    color: $pxh-docs-code-text-color;
  }
  pre {
    overflow-x: auto;
  }
}

